.Board {
    display: grid;
    padding: 4px;
    border-radius: 8px !important;
    align-items: center;
    grid-template-columns: repeat(5, 1fr);
    gap: 4px;
    margin: 0 auto;
    border-radius: 10px;
    box-shadow: 0 0 50px #fff, -10px 0 80px rgb(255, 0, 238), 10px 0 80px #0ff;
    animation: shadow 5s infinite linear;
}

@keyframes shadow {
    0% {
        box-shadow: 0 0 50px #fff, -5px 0 20px rgb(255, 0, 238), 5px 0 20px #0ff, 0px -5px 20px rgb(255, 0, 89), 0px 5px 20px yellow;
    }
    25% {
        box-shadow: 0 0 50px #fff, 0px -5px 20px rgb(255, 0, 238), 0px 5px 20px #0ff, 5px 0px 20px rgb(255, 0, 89), -5px 0 20px yellow;
    }
    50% {
      box-shadow: 0 0 50px #fff, 5px 0px 20px rgb(255, 0, 238), -5px 0 20px #0ff, 0px 5px 20px rgb(255, 0, 89), 0px -5px 20px yellow;

    }
    75% {
        box-shadow: 0 0 50px #fff, 0px 5px 20px rgb(255, 0, 238), 0px -5px 20px #0ff, -5px 0 20px rgb(255, 0, 89), 5px 0 20px yellow;
    }
    100% {
        box-shadow: 0 0 50px #fff, -5px 0 20px rgb(255, 0, 238), 5px 0 20px #0ff, 0px -5px 20px rgb(255, 0, 89), 0px 5px 20px yellow;
    }
}

@font-face {
    font-family: neon;
    src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.Board-title {
    display: flex;
    align-items: center;
    justify-content: center;
}

.neon-orange {
    font-family: neon;
    color: #fb4264;
    font-size: 80px;
    text-shadow: 0 0 3vw #f40a35;
    animation: neon-orange 1s ease infinite;
    -moz-animation: neon-orange 1s ease infinite;
    -webkit-animation: neon-orange 1s ease infinite;
}

.neon-blue {
    font-family: neon;
    color: #426dfb;
    font-size: 80px;
    text-shadow: 0 0 3vw #2356ff;
    animation: neon-blue 2s linear infinite;
    -moz-animation: neon-blue 2s linear infinite;
    -webkit-animation: neon-blue 2s linear infinite;
    -o-animation: neon-blue 2s linear infinite;
}

.head {
    gap: 20px;
}

.game {
    display: flex;
    flex-direction: column;
    gap: 40px;
    bottom: 80px;
}

.top {
    display: flex;
    justify-content: center;
    align-items: center;
}

.winner .neon-blue,
.winner .neon-orange {
    font-size: 200px;
}

@keyframes neon-orange {
    0%,
    100% {
        text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16, 0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
        color: #fed128;
    }
    50% {
        text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b, 0 0 5vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
        color: #806914;
    }
}

@keyframes neon-blue {
    0%,
    100% {
        text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff, 0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
        color: #28d7fe;
    }
    50% {
        text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
        color: #146c80;
    }
}

@media (max-width: 630px) {
    .top {
        scale: .9;
    }
}

@media (max-width: 500px) {
    .top {
        scale: .85;
    }
    .game {
        gap: 100px;
    }
}

@media (max-width: 400px) {
    .top {
        scale: .75;
    }
    .head {
        scale: .9;
    }
}